html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #f5f3f4;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 2;
}

[class$=contenedor] {
  width: 95%;
  max-width: 80rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  cursor: pointer;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
  margin: 0 auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1, h2, .heading-blanco, h3 {
  font-family: "Raleway", sans-serif;
  text-align: center;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2, .heading-blanco {
  font-size: 3.6rem;
  color: #5e311e;
}

h3 {
  font-size: 3.2rem;
}

.heading-blanco {
  color: #fff;
}
.heading-blanco span {
  color: #fff;
}
.heading-blanco::after {
  display: none;
}

[class$=__heading] {
  margin: 2rem 0;
}

@media (min-width: 450px) {
  .login {
    margin-top: 5rem;
  }
}
@media (min-width: 885px) {
  .login__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: 6rem 0 5rem 0;
  }
}
.login__imagenTelefono {
  display: none;
}
@media (min-width: 885px) {
  .login__imagenTelefono {
    display: block;
    width: 80%;
    margin-left: 6rem;
  }
}

.form {
  max-width: 45rem;
}
@media (min-width: 450px) {
  .form {
    padding: 3rem;
  }
}
@media (min-width: 885px) {
  .form {
    margin-right: 15rem;
    padding: 0;
  }
}
.form__login {
  padding: 1rem 3rem;
}
@media (min-width: 450px) {
  .form__login {
    border: 1px solid #ced4da;
    background-color: #fff;
    padding: 3rem;
  }
}
.form__heading {
  font-family: "Bilbo Swash Caps", cursive;
  font-size: 5.5rem;
  margin: 0 0 2rem 0;
}

.formulario__input {
  margin-bottom: 1rem;
  width: 100%;
  padding: 1rem;
  outline: none;
  border: 2px solid #ebe7de;
  border-radius: 5px;
  font-size: 1.4rem;
}
.formulario__submit {
  margin-top: 0.5rem;
  background-color: #00B4D8;
  padding: 1rem;
  width: 100%;
  cursor: pointer;
  color: #fff;
  border: 1px solid transparent;
  border-radius: 3px;
  font-size: 1.4rem;
  transition-property: background-color color;
  transition-duration: 0.3s;
}
.formulario__submit:hover {
  background-color: #fff;
  color: #00B4D8;
  border: 1px solid #00B4D8;
}

.iniciarSesion {
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  margin-top: 5rem;
}
.iniciarSesion__iniciar {
  color: #1877f2;
}
.iniciarSesion__facebook {
  color: #00B4D8;
  font-size: 2rem;
}
.iniciarSesion__olvidaste {
  color: #000;
  font-size: 1.4rem;
}

.registrarse {
  margin-top: 1rem;
  padding: 6rem 0;
  text-align: center;
}
.registrarse__cuenta {
  margin: 0;
  font-size: 1.4rem;
}
@media (min-width: 450px) {
  .registrarse {
    padding: 2rem 0;
    border: 1px solid #ced4da;
    background-color: #fff;
  }
}
.registrarse__registrar {
  color: #00B4D8;
  font-weight: 700;
}

@media (min-width: 885px) {
  .descargar {
    padding: 2rem 0;
  }
}
.descargar__app {
  text-align: center;
  font-size: 1.4rem;
}
.descargar__flex {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 1rem;
}
.descargar__img {
  width: 15rem;
}

.footer {
  padding: 8rem 0 3rem 0;
  text-align: center;
}
@media (min-width: 885px) {
  .footer {
    padding: 0;
    padding-bottom: 3rem;
  }
}
.footer__flex {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.footer__enlaces {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
  font-size: 1.2rem;
}
.footer__enlace {
  color: #000;
}

.copyright {
  font-size: 1.2rem;
  display: flex;
  justify-content: center;
  gap: 2rem;
}
.copyright__idioma {
  background-color: transparent;
  border: none;
  outline: none;
}
.copyright__2022 {
  font-size: 1.2rem;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #5e311e;
}

.margin-negativo-10 {
  margin-top: -10rem;
}

.bg-white {
  background-color: #fff;
}

.padding-bottom-0 {
  padding-bottom: 0;
}